export default {
	data() {
		return {
			name: '',
			sexList: [
				{name: '男', value: '1'},
				{name: '女', value: '2'}
			],
			marry:'',
			sexed:'',
			jobstatus:'',
			skill:'',
			sexSelect: [],
			birthday: '',
			education:'',
			educationList: [
				{name: '硕士', value: '1'},
				{name: '本科', value: '2'},
				{name: '大专', value: '3',}
			],
			educationSelect: [],
			marryList: [
				{name: '未婚', value: '1'},
				{name: '已婚', value: '2'}
			],
			marrySelect: [],
			jobList: [
				{name: '未就业', value: '1'},
				{name: '已就业', value: '2'}
			],
			jobSelect: [],
			skillList: [
				{name: '职业资格一级', value: '1'},
				{name: '职业资格二级', value: '2'},
				{name: '职业资格三级', value: '3',}
			],
			skillSelect: [],
			idNumber: '',
			idTypeList: [
				{name: '农业户口', value: '1'},
				{name: '非农业户口', value: '2'}
			],
			idTypeSelect: [],
			phone: '',
			farmerList: [
				{name: '是', value: '1'},
				{name: '否', value: '2'}
			],
			farmerSelect: [],
			industryList: [
				{name: '是', value: '1'},
				{name: '否', value: '2'}
			],
			industrySelect: [],
			address: '',
			email: '',
			time: '',
			model:'',
			modelList: [
				{name: '是', value: '1'},
				{name: '否', value: '2'}
			],
			modelSelect: [],
			difficultList: [
				{name: '是', value: '1'},
				{name: '否', value: '2'}
			],
			difficultSelect: [],
			farmer: '',
			industry:'',
			willingList: [
				{name: '是', value: '1'},
				{name: '否', value: '2'}
			],
			willingSelect: [],
			checkIdNumber(value) {
				var reg = /^[1-9]\d{5}(18|19|20)\d{2}((0[1-9])|(1[0-2]))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/;        
			    if(reg.test(value)){        
			        return {valid: true}          
			    }else{
			        return {valid: false, msg: '身份证号码格式错误'}          
			    }      
			},
		}
	},
	methods: {
		
		submit() {
			if(this.name == '') {
				this.$vux.toast.text('姓名不能为空')
				return
			}
			if(!this.sexSelect.length) {
				this.$vux.toast.text('请选择性别')
				return
			} else if(this.sexSelect[0] == 1) {
				this.sexed = '男'
			} else if(this.sexSelect[0] == 2) {
				this.sexed = '女'
			}
			if(this.birthday == '') {
				this.$vux.toast.text('请选择出生日期')
				return
			}
			if(!this.educationSelect.length) {
				this.$vux.toast.text('请选择文化程度')
				return
			} else if(this.educationSelect[0] == 1) {
				this.education = '硕士'
			} else if(this.educationSelect[0] == 2) {
				this.education = '本科'
			} else if(this.educationSelect[0] == 3) {
				this.education = '大专'
			}
			if(!this.marrySelect.length) {
				this.$vux.toast.text('请选择婚姻状态')
				return
			} else if(this.marrySelect[0] == 1) {
				this.marry = '未婚'
			} else if(this.marrySelect[0] == 2) {
				this.marry = '已婚'
			}
			if(!this.jobSelect.length) {
				this.$vux.toast.text('请选择就业状态')
				return
			}else if(this.jobSelect[0] == 1) {
				this.jobstatus = '未就业'
			} else if(this.jobSelect[0] == 2) {
				this.jobstatus = '已就业'
			}
			if(!this.skillSelect.length) {
				this.$vux.toast.text('请选择技术等级')
				return
			} else if(this.skillSelect[0] == 1) {
				this.skill = '硕士'
			} else if(this.skillSelect[0] == 2) {
				this.skill = '本科'
			} else if(this.skillSelect[0] == 3) {
				this.skill = '大专'
			}
			if(this.idNumber == '') {
				this.$vux.toast.text('请填写身份证号码')
				return
			}
			if(this.phone == '') {
				this.$vux.toast.text('请填写手机号码')
				return
			}
			if(!this.farmerSelect.length) {
				this.$vux.toast.text('请选择是否农民工')
				return
			} else if(this.farmerSelect[0] == 1) {
				this.farmer = '是'
			} else if(this.farmerSelect[0] == 2) {
				this.farmer = '否'
			}
			if(!this.industrySelect.length) {
				this.$vux.toast.text('请选择是否产业工')
				return
			} else if(this.industrySelect[0] == 1) {
				this.industry = '是'
			} else if(this.industrySelect[0] == 2) {
				this.industry = '否'
			}
			if(this.address == '') {
				this.$vux.toast.text('请输入地址')
				return
			}
			if(this.email == '') {
				this.$vux.toast.text('请输入邮箱地址')
				return
			}
			if(this.time == '') {
				this.$vux.toast.text('请选择入会时间')
				return
			}
			if(!this.modelSelect.length) {
				this.$vux.toast.text('请选择是否劳动模范')
				return
			}else if(this.jobSelect[0] == 1) {
				this.model = '未就业'
			} else if(this.jobSelect[0] == 2) {
				this.model = '已就业'
			}
			if(!this.difficultSelect.length) {
				this.$vux.toast.text('请选择是否困难职工')
				return
			} else if(this.difficultSelect[0] == 1) {
				this.difficult = '是'
			} else if(this.difficultSelect[0] == 2) {
				this.difficult = '否'
			}
			// if(!this.willingSelect.length) {
			// 	this.$vux.toast.text('请选择是否愿意办理银行卡')
			// 	return
			// }
			if(!this.$refs.idNumber.valid) {
				this.$vux.toast.text('身份证号码格式错误')
				return
			}
			if(!this.$refs.phone.valid) {
				this.$vux.toast.text('手机号码格式错误')
				return
			}
			if(!this.$refs.email.valid) {
				this.$vux.toast.text('邮箱格式错误')
				return
			}

			var data = {
				name: this.name,
				sex: this.sexed,
				birthday: this.birthday,
				card_number: this.idNumber,
				phone:this.phone,
				education:this.education,
				marry_status:this.marry,
				job_status:this.model,
				technical_level:this.skill,
				place:this.idNumber,
				is_peasant:this.farmer,
				is_industry: this.industry,
				company_address: this.address,
				email:this.email,
				join_at:this.time,
				is_model_worker:this.model,
				difficulty:this.difficult
			}
			console.log(data, '.then--------');
			this.$axios(this.$api.addInitiation , data , 'post')
			.then(res =>{
				console.log(res, '.then--------');
				if(res.error_code == 0){
					this.$vux.toast.text('提交成功')
					this.$router.go(-1)
					this.list = res.data
				}
			})
			.catch(res =>{
				console.log(res,'.catch--------')
			})
		},
		reset() {
			this.name = '',
			this.sexSelect = []
			this.birthday = ''
			this.educationSelect = []
			this.marrySelect = []
			this.jobSelect = []
			this.skillSelect = []
			this.idNumber = ''
			this.idTypeSelect = []
			this.phone = ''
			this.farmerSelect = []
			this.industrySelect = []
			this.address = ''
			this.email = ''
			this.time = ''
			this.modelSelect = []
			this.difficultSelect = []
			this.willingSelect = []
		}
	}
}



